@import url(https://fonts.googleapis.com/css?family=Raleway);
body {
    font-family: 'Raleway', sans-serif;
    margin: 0;
    padding: 0;
}

.container {
    max-width: 728px;
    margin: auto;
}

.clearfix:after {
    visibility: hidden;
    display: block;
    font-size: 0;
    content: " ";
    clear: both;
    height: 0;
}

/************************
        Nav
************************/

header {
    background: #334450;
    display: none;
}

nav {
    float: right;
}

nav ul {
    padding: 0;
    margin: 0;
}

nav ul li {
    display: inline;
    margin-left: 10px;
}

a {
    color: #EE5E63;
    text-decoration: none;
    -webkit-transition: .5s ease-in all;
    transition: .5s ease-in all;
}

a:hover {
    color: #333;
}

/************************
        Intro
************************/

.intro {
    background: #334450;
    height: 100vh;
    position: relative;
    overflow: hidden;
}

.intro-container {
    position: absolute;
    top: 50%;
    -webkit-transform: translate(-50%, -50%);
            transform: translate(-50%, -50%);
    left: 50%;
    color: #fff;
    text-align: center;
    width: 100%;
    opacity: 0;
    -webkit-animation: fadeIn 1s ease-in 8s forwards;
            animation: fadeIn 1s ease-in 8s forwards;
}

.intro h1 {
    color: #fff;
    font-size: 45px;
    margin-bottom: 12px;
}

.intro h1 span {
    color: #EE5E63;
}

.intro h2 {
    text-transform: uppercase;
}

.intro h2:after {
    content: '';
    height: 3px;
    width: 100px;
    background: #EE5E63;
    display: block;
    margin: 30px auto;
}

/************************
        Logo
************************/

.intro .logo {
    width: 100%;
    height: 100%;
    margin: auto;
    overflow: visible;
    -webkit-animation: fadeOut 1s ease-in 8s forwards;
            animation: fadeOut 1s ease-in 8s forwards;
}

.st0{fill:#CD5155;}
.st1{fill:#DE5A5D;}
.st2{fill:#E75D60;}
.st3{fill:#F16367;}
.st4{fill:#ED5F63;}
.st5{fill:#D75659;}
.st6{fill:#FFFFFF;}

.logo-container {
    width: 100%;
    max-width: 1200px;
    margin: auto;
    display: block;
    height: 100%;
    overflow: visible !important;
    margin: auto;
    -webkit-animation: logoRotate 4s ease-in 1 forwards;
            animation: logoRotate 4s ease-in 1 forwards;
}

.logo g, .logo svg {
    overflow: visible !important;
}


#logo-white {
    -webkit-animation: logoWhite 1s ease-in 4s 1 forwards;
            animation: logoWhite 1s ease-in 4s 1 forwards;
    -webkit-transform-origin: center center;
            transform-origin: center center;
    opacity: 0;
}

#backgroundPolygons {
    -webkit-transform-origin: center center;
            transform-origin: center center;
}

#backgroundPolygons polygon, #backgroundPolygons path {
    -webkit-transform-origin: center center;
            transform-origin: center center;
}



/************************
        Polygons
************************/

#poly1 {
    -webkit-animation: poly1 4s ease-in 1 forwards;
            animation: poly1 4s ease-in 1 forwards;
}

@-webkit-keyframes poly1 {
    0% {
        -webkit-transform: rotate(-180deg) scale(.1) translate(-1000%, 0);
                transform: rotate(-180deg) scale(.1) translate(-1000%, 0);
    }
    50% {
        -webkit-transform: scale(.1);
                transform: scale(.1);
    }
    100% {
        -webkit-transform: rotate(0) scale(1) translate(0,0);
                transform: rotate(0) scale(1) translate(0,0);
    }
}

@keyframes poly1 {
    0% {
        -webkit-transform: rotate(-180deg) scale(.1) translate(-1000%, 0);
                transform: rotate(-180deg) scale(.1) translate(-1000%, 0);
    }
    50% {
