@-webkit-keyframes logoRotate {
    0% {
        -webkit-transform: rotate(-360deg) scale(1);
                transform: rotate(-360deg) scale(1);
        opacity: 0;
    }
    35% {
        opacity: 1;
    }
    100% {
        -webkit-transform: rotate(0deg) scale(.3);
                transform: rotate(0deg) scale(.3);
        opacity: 1
    }
}


@keyframes logoRotate {
    0% {
        -webkit-transform: rotate(-360deg) scale(1);
                transform: rotate(-360deg) scale(1);
        opacity: 0;
    }
    35% {
        opacity: 1;
    }
    100% {
        -webkit-transform: rotate(0deg) scale(.3);
                transform: rotate(0deg) scale(.3);
        opacity: 1
    }
}


@-webkit-keyframes logoWhite {
    0% {
        opacity: 0;
    }

    100% {
        opacity: 1;
    }
}


@keyframes logoWhite {
    0% {
        opacity: 0;
    }

    100% {
        opacity: 1;
    }
}

@-webkit-keyframes fadeOut {
    0% {
        -webkit-transform: scale(1);
                transform: scale(1);
    }
    100% {
        -webkit-transform: scale(.3) translateY(100%);
                transform: scale(.3) translateY(100%);
    }
}

@keyframes fadeOut {
    0% {
        -webkit-transform: scale(1);
                transform: scale(1);
    }
    100% {
        -webkit-transform: scale(.3) translateY(100%);
                transform: scale(.3) translateY(100%);
    }
}

@-webkit-keyframes fadeIn {
    0% {
        opacity: 0;
    }
    100% {
        opacity: 1;
    }
}

@keyframes fadeIn {
    0% {
        opacity: 0;
    }
    100% {
        opacity: 1;
    }
}

/************************
        About
************************/

.about {
    width: 100%;
    padding: 100px 0;
    background: #333;
    color: #fff;
}

.about-left {
    width: 67%;
    float: left;
    margin-top: 50px;
    padding-right: 50px;
    box-sizing: border-box;
}

.about-left p {
    line-height: 26px;
}

.about-right {
    width: 33%;
    float: left;
    margin-top: 50px;
}

.about-right img {
    max-width: 100%;
    margin: auto;
    display: block;
}

/************************
        Contact
************************/

.contact {
    width: 100%;
    padding: 30px 0;
    background: #EE5E63;
    color: #fff;
    transition: all .5s ease-in;
    -moz-transition: all .5s ease-in;
    -webkit-transition: all .5s ease-in;
}

.contact-button {
    display: block;
    margin: auto;
    width: 100%;
}

.contact-button .contact:hover {
    background: #334450;
}

.contact .contact-text {
    text-align: center;
    font-size: 20px;
    color: #fff;
    text-transform: uppercase;
}

.contact .contact-text i {
    margin-right: 30px;
}

/************************
        Work
************************/

.work {
    width: 100%;
    padding: 100px 0 0;
    background: #fff;
    color: #333;
}

.work h2 {
    color: #333;
    border-bottom: 3px solid #EE5E63;
    margin-bottom: 30px;
    display: inline;
}

.work .portfolio-container {
    margin: 100px 0;
}

.work a {
    color: #333;
    display: block;
    margin: 40px 0;
    font-size: 40px;
    font-weight: bold;
    text-decoration: underline;
    /*border-bottom: 6px solid #333;*/
}

.work a:hover {
    color: #EE5E63;
    /*border-bottom: 6px solid #EE5E63;*/
}

/************************
        Skills
************************/

.skills {
    width: 100%;
    padding: 100px 0;
    background: #333;
    color: #fff;
}

.skills h2,
.jobs h2,
.about h2 {
    color: #fff;
    border-bottom: 3px solid #EE5E63;
    margin-bottom: 30px;
    display: inline;
}

.skills .tech-container {
    margin-top: 50px;
}

.skills h3 {
    text-transform: uppercase;
}

.skills .third {
    width: 40%;
    float: left;
}

.skills .two-thirds {
    width: 50%;
    float: right;
}

.skills .half {
    width: 50%;
    float: left;
    margin-top: 50px;
}

.skills .development {
    margin: 0;
    padding: 0;
    list-style-type: none;
}

.skills .development li {
    border-radius: 50%;
    height: 80px;
    width: 80px;
    border: 1px solid #fff;
    float: left;
    font-size: 12px;
    margin: 10px 10px 0 0;
    text-align: center;
    line-height: 82px;
    transition: all .5s ease-in;
    -moz-transition: all .5s ease-in;
    -webkit-transition: all .5s ease-in;
}

.skills .development li:hover {
    background: #EE5E63;
}

.skills .tools,
.skills .languages {
    margin: 0;
    padding: 0;
    list-style-type: none;
}

.skills .design {
    margin: 0;
    padding: 0;
}

.skills .design li {
    list-style-type: none;
    margin-right: 30px;
    display: inline;
}

.skills .design li img {
    width: 50px;
}

.skills i {
    margin-right: 10px;
}

.skills h3 i {
    font-size: 30px;
}

/************************
        Jobs
************************/
.jobs {
    width: 100%;
    padding: 100px 0;
    background: #334450;
}

.job-container {
    position: relative;
    height: 100%;
    width: 100%;
    overflow: auto;
    margin-bottom: 40px;
    margin-top: 50px;
}

.job {
    width: 50%;
    clear: both;
    position: relative;
    box-sizing: border-box;
}

.job:after {
    content: '';
    position: absolute;
    top: calc(50% - 14px);
    width: 16px;
    height: 16px;
    border: 6px solid #fff;
    border-radius: 50%;
    background: #334450;
    -webkit-transition: .5s ease-in all;
    transition: .5s ease-in all;
}

.job:nth-of-type(even) {
    float: left;
}

.job:nth-of-type(odd) {
    float: right;
}

.job:nth-of-type(even):after {
    right: -14px;
}

.job:nth-of-type(odd):after {
    left: -14px;
}

.job-content {
    width: 70%;
    text-align: center;
    background: #fff;
    position: relative;
    padding: 20px;
    z-index: 2;
    box-sizing: border-box;
    line-height: 24px;
}

.job:hover:after {
    background: #EE5E63;
}

.job-content:after {
    content: '';
    height: 30px;
    width: 30px;
    background: #fff;
    display: block;
    -webkit-transform: rotate(45deg);
            transform: rotate(45deg);
    top: calc(50% - 15px);
    right: -5px;
    position: absolute;
    z-index: 1;
}

.job:nth-of-type(odd) .job-content:after {
    left: -5px;
}

.job-content img {
    margin: auto;
    display: block;
    max-width: 100%;
}

.job:nth-of-type(even) .job-content {
    float: left;
}

.job:nth-of-type(odd) .job-content {
    float: right;
}

.job-line {
    position: absolute;
    top: 0;
    left: calc(50% - 1px);
    width: 2px;
    height: 100%;
    background: #EE5E63;
}

.job .position {
    font-weight: bold;
    margin-top: 20px;
}

/************************
        Footer
************************/

footer {
    clear: both;
    margin-top: 40px;
    padding: 20px 0;
    text-align: center;
    background: #fff;
}

footer ul {
    margin: 0 0 30px;
    padding: 0;
}

footer ul li {
    list-style-type: none;
    display: inline-block;
}

footer ul li a {
    font-size: 40px;
    margin: 0 20px;
}

/************************
        Animations
************************/
@-webkit-keyframes pulse { 100% { -webkit-transform: scale(1.1,1.1) translateX(-50%); } }
@keyframes pulse { 100% { -webkit-transform: scale(1.1,1.1) translateX(-50%); transform:scale(1.1,1.1) translateX(-50%); } }

/************************
        Mobile
************************/
@media (max-width: 767px) {
    .container {
        margin-left: 20px;
        margin-right: 20px;
    }
    .skills .half {
        width: 100%;
        clear: both;
        float: none;
    }
    .skills .third {
        width: 100%;
        clear: both;
        float: none;
    }
    .skills .two-thirds {
        width: 100%;
        clear: both;
        float: none;
        padding-top: 40px;
    }

    .job-line {
        display: none;
    }

    .job-content:after {
        display: none;
    }

    .job:after {
        display: none;
    }

    .job {
        width: 100%;
        margin: 15px 0;
    }

    .job-content {
        width: 100%;
    }

    .job-content img {
        max-width: 300px;
    }

    .intro-container {
        max-width: 90%;
        margin-left: 0 !important;
    }

    .about-left,
    .about-right {
        width: 100%;
        float: none;
    }
}

